
.button {
  height:60px;
  width:60px;
  margin:2px;
  padding-left:0px;
  padding-right:0px;
  text-align: center;
  -webkit-transition-duration: 0.4s; /* Safari */
  -o-transition-duration: 0.4s;
     transition-duration: 0.4s;
  cursor: pointer;
}
.button-rotate {
  height:60px;
  width:60px;
  margin:2px;
  padding-left:0px;
  padding-right:0px;
  text-align: center;
  -webkit-animation: 1s ease-out 0s 1 button-rotate-animation;
          animation: 1s ease-out 0s 1 button-rotate-animation;
}
@-webkit-keyframes button-rotate-animation{
  0% {
    -webkit-transform:  rotateY(0deg);
            transform:  rotateY(0deg);
    opacity: 1;
  }
  100% {
    -webkit-transform:  rotateY(180deg);
            transform:  rotateY(180deg);
    opacity: 0;
  }
}
@keyframes button-rotate-animation{
  0% {
    -webkit-transform:  rotateY(0deg);
            transform:  rotateY(0deg);
    opacity: 1;
  }
  100% {
    -webkit-transform:  rotateY(180deg);
            transform:  rotateY(180deg);
    opacity: 0;
  }
}
.button:hover {
  -webkit-transform: scale(1.2);
      -ms-transform: scale(1.2);
          transform: scale(1.2);
}
.display-value {
  height:60px;
  width:60px;
  border: 1px solid black;
  margin:2px;
  -webkit-transition-duration: 0.4s; /* Safari */
  -o-transition-duration: 0.4s;
     transition-duration: 0.4s;
}
.display-value-diamond {
  height:60px;
  width:60px;
  border: 1px solid black;
  margin:2px;
  -webkit-animation: 1s ease-out 0s 1 display-value-diamond-animation;
          animation: 1s ease-out 0s 1 display-value-diamond-animation;
}
@-webkit-keyframes display-value-diamond-animation {
  0% {
    -webkit-transform: scale(1.2) rotate(0deg);
            transform: scale(1.2) rotate(0deg);
  }
  25% {
    -webkit-transform: scale(1.2) rotate(15deg);
            transform: scale(1.2) rotate(15deg);
  }
  50% {
    -webkit-transform: scale(1.2) rotate(-15deg);
            transform: scale(1.2) rotate(-15deg);
  }
  75% {
    -webkit-transform: scale(1.2) rotate(0deg);
            transform: scale(1.2) rotate(0deg);
  }
  100% {
    -webkit-transform: scale(1) rotate(0deg);
            transform: scale(1) rotate(0deg);
  }
}
@keyframes display-value-diamond-animation {
  0% {
    -webkit-transform: scale(1.2) rotate(0deg);
            transform: scale(1.2) rotate(0deg);
  }
  25% {
    -webkit-transform: scale(1.2) rotate(15deg);
            transform: scale(1.2) rotate(15deg);
  }
  50% {
    -webkit-transform: scale(1.2) rotate(-15deg);
            transform: scale(1.2) rotate(-15deg);
  }
  75% {
    -webkit-transform: scale(1.2) rotate(0deg);
            transform: scale(1.2) rotate(0deg);
  }
  100% {
    -webkit-transform: scale(1) rotate(0deg);
            transform: scale(1) rotate(0deg);
  }
}
.images {
  height:60px;
  width:60px;
}
.right {
  -webkit-transform: rotate(180deg);
      -ms-transform: rotate(180deg);
          transform: rotate(180deg)
}
.down {
  -webkit-transform: rotate(90deg);
      -ms-transform: rotate(90deg);
          transform: rotate(90deg)
}
.up {
  -webkit-transform: rotate(270deg);
      -ms-transform: rotate(270deg);
          transform: rotate(270deg)
}



