.score-text {
  font-size: 40px;
  font-family: "Times New Roman", Times, serif;
  float: left;
}
.table-board{
  float: left;
}
.game-over-text{
  font-size: 40px;
  font-family: "Times New Roman", Times, serif;
}
.score-board{
  float: right;
}
.score-text-game-over {
  font-size: 40px;
  font-family: "Times New Roman", Times, serif;
}
.game-over-content
{
  text-align: center;
  -webkit-animation: game-over-content-highlight infinite 5s linear;
          animation: game-over-content-highlight infinite 5s linear;
}
@-webkit-keyframes game-over-content-highlight {
  0% { -webkit-transform: scale(1); transform: scale(1); }
  50% { -webkit-transform: scale(1.5); transform: scale(1.5); }
  100% { -webkit-transform: scale(1); transform: scale(1); }
}
@keyframes game-over-content-highlight {
  0% { -webkit-transform: scale(1); transform: scale(1); }
  50% { -webkit-transform: scale(1.5); transform: scale(1.5); }
  100% { -webkit-transform: scale(1); transform: scale(1); }
}
.score-text-score-decrement{
  color: red;
  font-size: 30px;
  font-weight: bold;
  opacity: 1;
  float: right;
  -webkit-animation:score-text-score-decrement-anim infinite 0.5s linear;
          animation:score-text-score-decrement-anim infinite 0.5s linear;
}
@-webkit-keyframes score-text-score-decrement-anim {
  0% { -webkit-transform: translateY(0px); transform: translateY(0px);opacity: 1; }
  100% { -webkit-transform: translateY(100px); transform: translateY(100px);opacity: 0; }
}
@keyframes score-text-score-decrement-anim {
  0% { -webkit-transform: translateY(0px); transform: translateY(0px);opacity: 1; }
  100% { -webkit-transform: translateY(100px); transform: translateY(100px);opacity: 0; }
}


